<template>
  <div class="shopcart">
    <van-nav-bar title="购物车" />
    <van-row class="goodslist" v-for="(item, index) in lists" :key="index">
      <van-col span="2">
        <van-checkbox
          :value="item.isSelect"
          @click="inputselect(index)"
        ></van-checkbox>
      </van-col>
      <van-col span="7">
        <van-image height="120" fit="cover" :src="item.image" />
      </van-col>
      <van-col span="15">
        <div class="content">
          <h1>{{ item.name }}</h1>
          <p class="price">￥{{ item.price }}</p>
          <p>
            <van-button
              type="default"
              size="mini"
              @click="reduce(item)">-</van-button>
            <span>{{ item.num }}</span>
            <van-button type="default" size="mini" @click="add(item)">+</van-button>
            <van-button
              type="danger"
              class="danger"
              size="mini"
              @click="del(index)"
              >删除</van-button
            >
          </p>
        </div>
      </van-col>
    </van-row>
    <div class="none">
      <van-empty
        class="custom-image"
        description="购物车还是空的，快来挑选好货吧"
        v-show="!$store.state.cart.lists.length"
      >
        <button @click="goPage('Home')">去逛逛</button>
      </van-empty>
    </div>
    <img
      class="likes"
      src="http://oss.suning.com/vss/activity/wximg/cart/cnxh-title.png"
    />
    <liked></liked>
    <van-submit-bar
      v-show="$store.state.cart.lists.length"
      :price="total * 100"
      button-text="提交订单"
      @submit="goPage('Order')"
    >
      <van-checkbox @click="checkedAll" :value="checked">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
import liked from "../../components/Liked";
import "../../assets/css/shopcart.less";
export default {
  data() {
    return {
      checked: true,
      total: 0,
      lists: [],
    };
  },
  components: {
    liked,
  },
  methods: {
    reduce(item){
      item.num <= 1 ? 1 : item.num--;
      this.lists.push("0");
      this.lists.pop()
    },
    add(item){
      item.num++;
      this.lists.push("0");
      this.lists.pop()
    },
    goPage(name) {
      this.$router.push({ name });
    },
    del(index) {
      this.lists.splice(index, 1);
    },
    sum() {
      this.total = this.lists.reduce((total, item) => {
        if (!item.isSelect) return total;
        return total + parseInt(item.price) * parseInt(item.num);
      }, 0);
    },
    checkedAll() {
      this.lists.forEach((item) => {
        item.isSelect = !this.checked;
      });
      this.checked = !this.checked;
    },
    checkboxclick() {
      this.checked = this.lists.every((item) => {
        return item.isSelect;
      });
    },
    inputselect(index) {
      this.lists[index].isSelect = !this.lists[index].isSelect;
      this.checkboxclick();
      this.sum();
    },
  },
  created() {
    this.lists = this.$store.state.cart.lists;
    
    this.checkboxclick();
    this.sum();
  },
  beforeUpdate() {
    this.sum();
  },
  beforeDestroy() {
    this.$store.commit("cart/edit", this.lists);
  },
};
</script>

<style lang="less" scoped>
</style>